/* ==================== Media queries ======================= */
/*==========  Desktop First Method  ==========*/
@media only screen and (max-width : 1900px) {
}
/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {
    .wrapper-w {
        width: 96%;
        max-width: 1110px;
    }
    .main-slider-section {
        height: auto;
        max-height: none;
    }
    .main-slider-item:before {
        display: none;
    }
    .main-slider-heading {
        font-size: 60px;
    }
    .main-slider-text {
        position: static;
        display: block;
        width: 100%;
        text-align: center;
        transform: none;
    }
    .main-slider-item {
        text-align: center;
    }
    .category-text {
        font-size: 20px;
        max-width: 100%;
        bottom: 3%;
        line-height: 1;
    }
    .smallForm-section .wrapper {
        flex-wrap: wrap;
        text-align: center;
    }
    .smallForm-container {
        text-align: center;
    }
    .smallForm-heading {
        margin-bottom: 10px;
    }
    .smallForm-heading,
    .smallForm-container {
        display: block;
        width: 100%;
    }
    .works-slide-imgPart {
        position: static;
    }
    .works-slide-textPart {
        max-width: 100%;
        margin-bottom: 15px;
    }
    .works-slider-item {
        height: auto;
    }
    .works-slide-imgPart .heading {
      font-size: 25px;
      padding-left: 1%;
      bottom: 2%;
    }
    .manuf-slide-imgPart {
        position: static;
        img {
            margin: 0 auto;
        }
    }
    .manuf-slide-textPart {
        padding: 1%;
        min-height: 0px;
    }
    .js-manufactory-slider .slick-dots {
        left: auto;
        right: 43%;
        bottom: 0%;
    }
    .manufactory-section .slider-custom-arrows {
        left: auto;
        right: 21%;
        bottom: 0px;
    }
    .manuf-slide-imgPart .heading {
        font-size: 25px;
        padding-left: 1%;
        bottom: 2%;
        right: auto;
    }
    .main-form-container {
        position: static;
        margin: 10px auto;
    }
    .leaves {
        display: none;
    }
}
/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
    .header-top {
        flex-wrap: wrap;
    }
    .logo {
        order: -1;
        flex-basis: 100%;
        margin-right: 0 !important;
        margin-bottom: 15px;
    }
    .category-item {
        width: 33.3%;
    }
    .works-slider-container .slider-custom-arrows,
    .manufactory-section .slider-custom-arrows {
        position: static;
        margin-top: 15px;
    }
    .js-works-slider .slick-dots,
    .js-manufactory-slider .slick-dots {
        position: static;
    }
    .works-slide-imgPart,
    .manuf-slide-imgPart {
        width: 100%;
        img {
            max-height: 100vh;
        }
    }
    .button.slide-button {
        margin-bottom: 15px;
    }
    .features-part,
    .about-text-part {
        padding: 3% .8% 2% 3%;
    }
    .footer-top {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
        justify-content: center;
    }
    .footer-logo {
        margin-right: 12px;
        margin-bottom: 12px;
    }
    .footer-nav {
        flex-basis: 100%;
    }
    .footer-bottom {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
        justify-content: center;
    }
    .copy {
        flex-basis: 100%;
        text-align: center;
    }
    .header {
        position: relative;
    }
    body {
        padding-top: 0;
    }
}
/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
    .about-section .wrapper {
        white-space: normal;
    }
    .features-part, .about-text-part {
        display: block;
        width: 100%;
    }
}
@media only screen and (max-width : 680px) {
    p {
        font-size: 14px;
        line-height: 1.5;
    }
    .header-top {
        text-align: center;
        justify-content: center;
    }
    .header-button,
    .top-contacts {
        flex-basis: 100%;
        text-align: center;
    }
    .header-nav-links {
        flex-wrap: wrap;
        li {
            flex-basis: 50%;
            margin-bottom: 10px;
            text-align: center;
        }
    }
    .category-item {
        width: 50%;
    }
    .heading {
        font-size: 28px;
    }
    .small-form {
        input {
            display: block;
            width: 100%;
            margin-bottom: 20px !important;
        }
    }
    .button.slide-button {
        height: 40px;
        line-height: 40px;
        font-size: 14px;
    }
    .works-slider-item .button {
        width: 150px;
    }
   .footer .footer-nav-links {
        -webkit-flex-wrap: wrap;
        -moz-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
        -o-flex-wrap: wrap;
        flex-wrap: wrap;
        justify-content: center;
        li {
            flex-basis: 50%;
            text-align: center;
            padding: 0 10px;
        }
    }
}
/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {
    .header.fixed .header-button {
        margin-bottom: 15px;
    }
    .header-nav-links li {
        flex-basis: 100%;
    }
    .main-slider-heading {
        font-size: 40px;
    }
    .collection-link {
        padding-left: 0;
        margin-left: -31px;
    }
    .collection-link:before {
        display: none;
    }
    .testimonials-heading {
        font-size: 28px;
    }
    .testimonials-subheading {
        font-size: 16px;
    }
    .form-heading {
        font-size: 20px;
    }
    .form-heading {
        font-size: 17px;
    }
}
@media only screen and (max-width : 375px) {
    .category-item {
        width: 100%;
    }
    .feature {
        text-align: center;
    }
    .feature-manufacturing > span {
        width: 100%;
        margin-right: 0;
        text-align: center;
    }
    .feature-img {
        margin: 0 auto 17px;
    }
    .features-part h3 {
        max-width: 100%;
        text-align: center;
    }
    .footer .footer-nav-links li {
        flex-basis: 100%;
    }
    .footer {
        .phone,
        .mail {
            display: block;
            margin: 10px auto;
            text-align: center;
        }
    }
}

@media only screen and (min-width : 1024px) {
    .main-slider-heading {
        left: 150vw;
    }
    .collection-link  {
        left: -150vw;
    }
    .main-slider-item-bg {
        top: -200%;
    }
    .main-slider-item-img {
        top: 100vh;
    }
}
